const parseEdges = record =>
  record.SDGRecords.filter(item => item.Targets.length > 1).map(item => ({
    source: String(item.Targets[0]),
    target: String(item.Targets[1]),
    style: {
      stroke:
        item.Interaction.value === "Positive"
          ? "green"
          : item.Interaction.value === "Negative"
          ? "red"
          : "grey"
    }
    // direction: item.Interaction.direction,
    // type: item.Interaction.type,
    // value: item.Interaction.value,
    // dashes: item.Interaction.type === "Direct" ? true : false,
  }));

const parseData = (goals, targets, links) => {
  const edges = links
    .filter(record => record.SDGRecords.length > 0)
    .flatMap(parseEdges);

  const possibleNodes = [
    ...new Set(
      edges.flatMap(item => [
        item.source.split(".")[0],
        item.target.split(".")[0]
      ])
    )
  ].map(Number);

  const targetNodes = targets
    .filter(item => item.id.match("[0-9]$"))
    .map(item => ({
      id: String(item.id),
      data: {
        label: item.id
      },
      size: 25,
      shape: "CircleNode"
      // title: item.title,
      // group: item.goal,
      // color: goals[item.goal - 1].colorInfo.hex,
      // image: goals[item.goal - 1].icon_url,
      // level: 2,
    }))
    .filter(item => possibleNodes.includes(item.group));

  const goalNodes = goals
    .filter(item => possibleNodes.includes(item.goal))
    .map(item => ({
      id: String(item.goal),
      data: {
        label: String(item.goal)
      },
      size: 60,
      shape: "CircleNode"
      // title: item.short,
      // group: item.goal,
      // color: item.colorInfo.hex,
      // image: item.icon_url,
      // level: 1,
    }));

  const allNodes = [...goalNodes, ...targetNodes];

  const parent2child = targetNodes.map(item => ({
    source: item.group,
    target: item.id,
    style: {
      stroke: "royalblue"
    }
    // dashes: false,
    // direction: "ltr",
    // type: "Direct",
    // value: "p2c",
    // color: "grey"
  }));

  const allEdges = [...parent2child, ...edges];

  const data = { nodes: allNodes, edges: allEdges };
  return data;
};

const recordNodes = parseData(GoalList, TargetList, Records);
export default recordNodes;
